import { Component, EventEmitter, Input, Output } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';
import { Authenticator, Factors } from '@profile-management/data-access';
import { ImageSrcInterceptorDirective } from '@profile-management/util';
import { ManageAuthenticatorsIconComponent } from '@profile-management/manage-authenticators/ui';

@Component({
  standalone: true,
  imports: [CommonModule, TranslateModule, ManageAuthenticatorsIconComponent, ImageSrcInterceptorDirective],
  selector: 'tg-pm-manage-authenticators-manage',
  templateUrl: './manage-authenticators-manage.component.html',
})
export class ManageAuthenticatorsManageComponent {
  @Input() selectedAuthenticator: Authenticator | undefined | null;
  @Input() successPageUrl: string;

  @Output() toggleManageEvent: EventEmitter<boolean> = new EventEmitter<boolean>();

  showManage = false;

  manageAuthenticator() {
    if (this.selectedAuthenticator?.type === Factors.SMS) {
      window.location.href = this.successPageUrl;
    }
  }

  cancel() {
    this.showManage = !this.showManage;
    this.toggleManageEvent.emit(false);
  }
}
